.button-options {
  padding: 10px;
  border-top: 1px solid #ccc;
}

.button-options button {
  padding: 10px 15px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  margin: 5px 5px 0 0;
  white-space: nowrap;
  transition: background-color 0.3s ease;
}

.btn-green {
  background-color: #4CAF50;
  color: white;
}

.btn-green:hover {
  background-color: #45a049;
}

.btn-orange {
  background-color: #FF9800;
  color: white;
}

.btn-orange:hover {
  background-color: #fb8c00;
}

@media (max-width: 480px) {
  .button-options button {
    width: 100%;
  }
}
