.chat-input {
  display: flex;
  border-top: 1px solid #ccc;
}

.chat-input input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-radius: 20px;
  margin: 10px;
  outline: none;
}

.chat-input button {
  padding: 10px 15px;
  border: none;
  border-radius: 20px;
  background-color: #4CAF50;
  color: white;
  cursor: pointer;
  margin: 10px 10px 10px 0;
  transition: background-color 0.3s ease;
}

.chat-input button:hover {
  background-color: #45a049;
}
