.message-list {
  flex: 1;
  padding: 15px;
  overflow-y: auto;
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
}

.message {
  max-width: 75%;
  padding: 12px 15px;
  border-radius: 12px;
  margin-bottom: 12px;
  line-height: 1.4;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.message.bot {
  background-color: #FFF9C4;
  align-self: flex-start;
}

.message.user {
  background-color: #CFD8DC;
  align-self: flex-end;
  text-align: right;
}
